
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --off-white: black;
}
/* reset for all browsers selecting all elmenst to have ) padding etc. */
* {
  list-style: none;
  padding :0px;
}

.nav {
  background-color: black;
  background-color: var(--off-white);
  position: -webkit-sticky;
  position: sticky;
  top: 0px;
  width: 100%;
  height: 80px;
  z-index: 1000;
  box-shadow: 0 4px 14px 0 rgba(0, 0, 0, 0.15);
  
}

.nav-logo {
  height: 60px;
  width: 60px;
}

.nav-logo:hover {
  cursor: pointer;
}

.nav-content {
  
  padding: 0rem 3rem;
  margin: 0 auto;
  display: flex; 
  justify-content: space-between;
  
  align-items: center;
  height: 100%;
}

.nav-item {
  display: inline-flex;
  margin-left: 2rem;
  color: blue;
  font-size: 1.2em;
  font-family: 'Exo 2', sans-serif;
  
}

.nav-item:hover {
  color:blue;
  cursor: pointer;
}


.section-content {
